import React from 'react';
import { Helmet } from 'react-helmet';

const Meta = ({ title, description, keywords }) => {
  return (
    <Helmet>
      <title>{title}</title>
      <meta name="description" content={description} />
      <meta name="keyword" content={keywords} />
    </Helmet>
  );
};

Meta.defaultProps = {
  title: 'Welcome To MamaIfy\'s\' Shop',
  description: 'We sell the best food products for cheap',
  keywords: 'Fresh Vegetables, food stuffs and provisions',
};

export default Meta;
